import { Injectable, Inject, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { DOCUMENT } from '@angular/common';
import { Observer, Observable } from 'rxjs';

import {
  ITokenModel,
  ITokenService,
  DA_SERVICE_TOKEN,
} from '../token/interface';

const OPENTYPE = '_delonAuthSocialType';
const HREFCALLBACK = '_delonAuthSocialCallbackByHref';

export type SocialOpenType = 'href' | 'window';

@Injectable()
export class SocialService implements OnDestroy {
  private _win: Window;
  private _win$: any;
  private observer: Observer<ITokenModel>;

  constructor(
    @Inject(DA_SERVICE_TOKEN) private tokenService: ITokenService,
    @Inject(DOCUMENT) private doc: any,
    private router: Router,
  ) {}

  /**
   * ä½¿ç¨çªä½æå¼ææé¡µï¼è¿åå¼æ¯ `Observable<ITokenModel>` ç¨äºè®¢éææåè¿åçç»æ
   * @param url è·åææå°å
   * @param callback åè°è·¯ç±å°å
   * @param options.windowFeatures ç­å `window.open` ç `features` åæ°å¼
   */
  login(
    url: string,
    callback?: string,
    options?: {
      type?: 'window';
      windowFeatures?: string;
    },
  ): Observable<ITokenModel>;

  /**
   * è·³è½¬è³ææé¡µ
   * @param url è·åææå°å
   * @param callback åè°è·¯ç±å°å
   */
  login(
    url: string,
    callback?: string,
    options?: {
      type?: 'href';
    },
  ): void;

  /**
   * è·³è½¬è³ç»å½é¡µï¼è¥ä¸º `type=window` æ¶ï¼è¿åå¼æ¯ `Observable<ITokenModel>`
   * @param url è·åææå°å
   * @param callback å½ `type=href` æåæ¶çåè°è·¯ç±å°å
   * @param options.type æå¼æ¹å¼ï¼é»è®¤ `window`
   * @param options.windowFeatures ç­å `window.open` ç `features` åæ°å¼
   */
  login(
    url: string,
    callback: string = '/',
    options: {
      type?: SocialOpenType;
      windowFeatures?: string;
    } = {},
  ): Observable<ITokenModel> | void {
    options = Object.assign(
      {
        type: 'window',
        windowFeatures:
          'location=yes,height=570,width=520,scrollbars=yes,status=yes',
      },
      options,
    );
    localStorage.setItem(OPENTYPE, options.type);
    localStorage.setItem(HREFCALLBACK, callback);
    if (options.type === 'href') {
      this.doc.location.href = url;
      return;
    }

    this._win = window.open(url, '_blank', options.windowFeatures);
    this._win$ = setInterval(() => {
      if (this._win && this._win.closed) {
        this.ngOnDestroy();

        let model = this.tokenService.get();
        if (model && !model.token) model = null;

        // è§¦ååæ´éç¥
        if (model) {
          this.tokenService.set(model);
        }

        this.observer.next(model);
        this.observer.complete();
      }
    }, 100);
    return Observable.create((observer: Observer<ITokenModel>) => {
      this.observer = observer;
    });
  }

  /**
   * æææååçåè°å¤ç
   *
   * @param rawData æå®åè°è®¤è¯ä¿¡æ¯ï¼ä¸ºç©ºæ¶ä»æ ¹æ®å½åURLè§£æ
   */
  callback(rawData?: string | ITokenModel): ITokenModel {
    // from uri
    if (!rawData && this.router.url.indexOf('?') === -1) {
      throw new Error(`url muse contain a ?`);
    }
    // parse
    let data: ITokenModel = { token: `` };
    if (typeof rawData === 'string') {
      const rightUrl = rawData.split('?')[1].split('#')[0];
      data = <any>this.router.parseUrl('./?' + rightUrl).queryParams;
    } else {
      data = rawData;
    }

    if (!data || !data.token) throw new Error(`invalide token data`);
    this.tokenService.set(data);

    const url = localStorage.getItem(HREFCALLBACK) || '/';
    localStorage.removeItem(HREFCALLBACK);
    const type = localStorage.getItem(OPENTYPE);
    localStorage.removeItem(OPENTYPE);
    if (type === 'window') {
      window.close();
    } else {
      this.router.navigateByUrl(url);
    }

    return data;
  }

  ngOnDestroy(): void {
    clearInterval(this._win$);
    this._win$ = null;
  }
}
